<template>
  <div class="home" :class="itsGreen ? 'lockScreen' : ''">
    <!-- <Openning /> -->
    <div class="banner w-100" :style="`background-image: url(${require(`@/assets/banner-bg.png`)})`">
        <Sidebar  @changeItsGreen="changeItsGreen"/>
        <Banner />
    </div>
    <About />
    <YourServices />
    <!-- <Partners /> -->
    <Info />
    <Blog />
    <Footer @changeFooter="changeFooter"/>
    <Contact :itsGreen="itsGreen || inFooter ? true : false"/>
  </div>
</template>

<script>
import Banner from "./components/Banner.vue"
import Sidebar from "./components/Sidebar.vue"
import About from "./components/About.vue"
import YourServices from "./components/YourServices.vue"
// import Partners from "./components/Partners.vue"
import Info from "./components/Info.vue"
import Blog from "./components/Blog.vue"
import Footer from "./components/Footer.vue"
import Contact from "./components/Contact.vue"
// import Openning from "./components/Openning/Openning.vue"

export default {
    components:{
        Banner,
        Sidebar,
        About,
        YourServices,
        // Partners,
        Info,
        Blog,
        Footer,
        Contact,
        // Openning
    },
    data(){
        return {
            itsGreen: false,
            inFooter: false
        }
    },
    methods: {
        changeItsGreen( value ){
            this.itsGreen = value
        },
        changeFooter( value ){
            this.inFooter = value
        }
    }
}
</script>

<style scoped >
.banner{
    height: 100vh;
    padding-left: calc(20%);
    padding-right: calc(20%);
    padding-top: 50px;
}

@media (max-width: 1250px) {
    .banner{
        padding-left: calc(15%);
        padding-right: calc(15%);
        padding-top: 0px;
    }
}

@media (max-width: 930px) {
    .banner{
        padding-left: 0px;
        padding-right: 0px;
    }

    .lockScreen {
        height: 100vh !important;
        overflow: hidden !important;
    }
}
</style>