<template>
    <div class="login d-flex justify-content-center align-items-center">
        <div class="login-form d-flex justify-content-center">
            <DynamicIcon icon="dote-logo-white"/>

            <b-form-group id="input-group-1" label="Login:" label-for="input-1" class="mt-4 col-12">
                <b-form-input id="input-1" v-model="form.login" type="email" placeholder="Login" required />
            </b-form-group>

            <b-form-group id="input-group-2" label="Senha:" label-for="input-2" class="mt-2 col-12">
                <b-form-input id="input-2" v-model="form.password" placeholder="Senha" required />
            </b-form-group>

            <b-button class="btn-base primary-btn btn-logar mt-4" @click="logar">
                Entrar
            </b-button>

            <b-alert show variant="danger" v-if="isIncorrect" class="alert w-100 mt-3">
                Os Dados estão incorretos.
            </b-alert>
        </div>    
    </div>
</template>

<script>
import DynamicIcon from "@/components/commons/dynamicIcon/DynamicIcon.vue"

export default {
    components:{
        DynamicIcon
    },
    data(){
        return {
            form:{
                login: null,
                password: null
            },
            isIncorrect: null
        }
    },
    methods: {
        logar(){
            this.isIncorrect = null 
            if(this.form.login == "admin" && this.form.passwrd == "admiin"){
                this.$emit("logar", true)
            } else {
                this.isIncorrect = true
            }
        },
        deslogar(){
            this.$emit("logar", false)
        }
    }
}
</script>

<style scoped >
.login{
    width: 100vw;
    height: 100vh;
}

.login-form{
    height: 300px;
    width: 300px;
    flex-wrap: wrap;
}

.btn-logar{
    padding: 5px 20px;
    float: right;
    border-radius: 5px;
    font-size: 20px;
}

.alert{
    padding: 10px 10px;
}

</style>