<template>
    <div class="contact-bar justify-content-between align-items-center" :class="itsGreen ? 'green' : ''">
        <p>Fale Conosco</p>
        <div class="line"/>
        <div class="contact-bar-btns">
            <DynamicIcon icon="messenger-color" @click="openFb" v-if="!itsGreen"/>
            <DynamicIcon icon="messenger-black" @click="openFb" v-else/>
            <DynamicIcon icon="wtp-color" @click="openWtp" v-if="!itsGreen"/>
            <DynamicIcon icon="wtp-black" @click="openWtp" v-else/>
        </div>
    </div>
</template>

<script>
import DynamicIcon from "@/components/commons/dynamicIcon/DynamicIcon.vue"

export default {
    components:{
        DynamicIcon
    },
    props:{
        itsGreen:Boolean
    },
    methods:{
        abrirNovaAba( url ) {
            window.open( url, '_blank');
        },
        openWtp(){
            this.abrirNovaAba('')
        },
        openFb(){
            this.abrirNovaAba('')
        }
    }
}
</script>

<style scoped>
.contact-bar{
    position: fixed;
    bottom: 0px;
    height: 50px;
    width: 100vw;
    background-color: white;
    transition: 0.2s;
    z-index: 10000;
    padding-left: 35px;
    padding-right: 35px;
    display: none;
}

.contact-bar p{
    margin: 0;
    color: #000;
    font-weight: 600;
    font-size: 18px;
    line-height: 21.6px;
}

.contact-bar .line{
    height: 2px;
    width: 75px;
    background-color: #000;
}

.contact-bar .contact-bar-btns img{
    height: 22px;
}

.contact-bar .contact-bar-btns img:first-child{
    margin-right: 30px;
}

html body .contact-bar.green{
    background-color: #64FFB1;
}

@media (max-width: 930px) {
    .contact-bar{
        display: flex;
    }
}
</style>
