<template>
    <div class="w-100" :style="`background-image: url(${require(`@/assets/banner-cta.png`)})`">
        <intersect @enter="onIntersect">
            <div class="info-content w-100 row m-0">
                <div class="row m-0 px-0 info-text-content">   
                    <p class="col-12 m-0 p-0 info-text text-center" :class="{ 'fade-item-in-down-to-up': intersecting }">
                        Você não vai se arrepender.
                    </p>       
                    <p class="col-12 m-0 p-0 info-text highlight text-center" :class="{ 'fade-item-in-down-to-up': intersecting }">
                        95%+ dos nossos clientes continuam conosco.
                    </p>
                </div>  
            </div>
        </intersect>             
    </div>
</template>

<script>
import Intersect from 'vue-intersect'

export default {
    components:{
        Intersect
    },
    data(){
        return {
            intersecting: false
        }
    },
    methods: {
        onIntersect(entries) {
            if (entries[0].isIntersecting) {
                this.intersecting = true;
            }
        }
    }
}
</script>

<style scoped>
html body .info-content{
    padding: 25px calc(35%) 50px calc(35%);
    backdrop-filter: blur(8px);
}

html body .info-text-content{
    padding-top: 100px;
    padding-bottom: 75px;
}

html body .info-text{
    font-weight: 500;
    font-size: 32px;
}

html body .info-text.highlight{
    font-size: 40px;
}

@media (max-width: 1250px) {
    .info-content{
        padding-left: calc(15%);
        padding-right: calc(15%);
    }
}

@media (max-width: 930px) {
    html body .info-content{
        padding-left: 20px !important;
        padding-right: 20px !important;
        backdrop-filter: blur(3px);
    }

    html body .info-text-content{
        padding-top: 30px;
        padding-bottom: 15px;
    }

    html body .info-text{
        font-size: 22px;
    }

    html body .info-text.highlight{
        font-size: 26px;
    }
}
</style>